import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import { Link } from "react-router-dom";
import Grid from "@material-ui/core/Grid";
import { useStore } from "../../constants/hookstore";
import { useFetch } from "../../constants/hooks";

const useStyles = makeStyles({
  root: {
    maxWidth: 345
  }
});

export default function MovieDetail() {
  const classes = useStyles();

  // *start movie logic
  console.log("this is movie detail");

  const MovieId = useStore();
  //console.log(store);
  console.log(MovieId[0]);

  const movie_id = MovieId[0];
  console.log(movie_id);

  const [data] = useFetch(
    `https://api.themoviedb.org/3/movie/${movie_id}?api_key=dc15dc94ad3132023e29552c1fe96161&language=en-US`
  );

  const imagepath = `https://image.tmdb.org/t/p/w500/${data.poster_path}`;

  //*end movie logic*

  return (
    <Grid
      container
      spacing={5}
      direction='column'
      alignItems='center'
      justify='center'
      style={{ minHeight: "100vh" }}
    >
      <Grid item xs={10}>
        <Card className={classes.root}>
          <CardActionArea>
            <CardMedia
              component='img'
              alt='{data.original_title}'
              // height='500'
              // width='350'
              image={imagepath}
              title={data.original_title}
            />
            <CardContent>
              <Typography gutterBottom variant='h5' component='h2'>
                {data.original_title}
              </Typography>
              <Typography variant='body2' color='textSecondary' component='p'>
                {data.overview}
              </Typography>
            </CardContent>
          </CardActionArea>
          <CardActions>
            <Button size='small' color='primary'>
              Share
            </Button>
            <Button size='small' color='primary'>
              Learn More
            </Button>
          </CardActions>
        </Card>
      </Grid>
    </Grid>
  );
}
